    background-color: #333;
    background-color: var(--dark);
    color: whitesmoke;
    color: var(--light);
    min-height: 100vh;
    display: flex;
    justify-content: center;
}

#root {
    width: 1280px;
    margin-top: 72px;
    padding: 24px;
