}

label {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-bottom: 24px;
    grid-gap: 8px;
    gap: 8px;
}

button {
    font-family: 'Fira Sans', sans-serif;
    font-size: 16px;
    padding: 12px 24px;
    line-height: 1;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    min-height: 40px;
    background-color: cornflowerblue;
    background-color: var(--primary);
    color: whitesmoke;
    color: var(--light);
}

button:hover {
    opacity: 0.9;
}

button:disabled {
    opacity: 0.4;
    cursor: not-allowed;
}

input {
    font-family: 'Fira Sans', sans-serif;
    font-size: 16px;
    outline: none;
    border: 2px solid whitesmoke;
    border: 2px solid var(--light);
    border-radius: 4px;
    height: 40px;
    padding: 8px;
}

input:focus {
    border-color: cornflowerblue;
    border-color: var(--primary);
}

select {
    font-family: 'Fira Sans', sans-serif;
    font-size: 16px;
    padding: 8px;
    outline: none;
    cursor: pointer;
    border-radius: 4px;
    height: 40px;
}

table {
    width: 100%;
    border: 1px solid #555;
    border: 1px solid var(--darkLight);
}

thead, tfoot {
    background: #555;
    background: var(--darkLight);
}

tr {
}

tr:hover {
    background: #555;
    background: var(--darkLight);
}

th, td {
    padding: 8px;
    position: relative;
}

